<template>
  <div class="app flex-row align-items-center animated fadeIn">
    <div class="container">
      <div class="row justify-content-center">
        <div class="col-md-6">
          <div class="card-group mb-0">
            <div class="card p-4">
              <div class="card-body">
                <h2></h2>
                <p class="text-muted">     .</p>
                <b-form @submit.prevent="onSubmit">
                  <b-input-group class="mb-2">
                    <b-input-group-addon><i class="icon-user"></i></b-input-group-addon>
                    <b-form-input v-model="credentials.email" type="text" placeholder=" " autofocus="true"></b-form-input>
                  </b-input-group>
                  <b-input-group class="mb-2">
                    <b-input-group-addon><i class="icon-lock"></i></b-input-group-addon>
                    <b-form-input v-model="credentials.password" type="password" placeholder=" " autofocus="true"></b-form-input>
                  </b-input-group>

                  <b-form-checkbox class="mb-4" v-model="rememberEmail" value="accepted" unchecked-value="not_accepted">
                     
                  </b-form-checkbox>

                  <b-row>
                    <b-col>
                      <b-btn type="submit" variant="primary"></b-btn>
                    </b-col>
                    <b-col class="text-right">
                      <b-btn type="button" variant="link" :to="'/forgot-password'" class="px-0"> ?</b-btn>
                    </b-col>
                  </b-row>
                </b-form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Validator } from 'simple-vue-validator'
import { mapActions } from 'vuex'

export default {
  name: 'Login',

  validators: {
    'credentials.email' (value) {
      return Validator.value(value).required().email('  .')
    },

    'credentials.password' (value) {
      return Validator.value(value).required()
    }
  },

  data () {
    return {
      credentials: {
        email: null,
        password: null
      },

      rememberEmail: 'not_accepted'
    }
  },

  watch: {
    rememberEmail (val) {
      if (val === 'accepted') {
        this.$cookie.set('remember', 'accepted', 1)
      } else {
        this.$cookie.set('remember')
        this.$cookie.delete('email')
      }
    }
  },

  methods: {
    ...mapActions([
      'login'
    ]),

    onSubmit () {
      this.$validate()
        .then(success => {
          if (success) {
            if (this.rememberEmail === 'accepted') {
              this.$cookie.set('email', this.credentials.email, { expires: '1D' })
            }

            this.login(this.credentials)
            this.$router.push('/')
          } else {
            alert('/  .')
          }
        })
    }
  },

  created () {
    this.rememberEmail = this.$cookie.get('remember')

    if (this.rememberEmail === 'accepted') {
      this.credentials.email = this.$cookie.get('email')
    }
  }
}
</script>


