import Vue from 'vue'
import Router from 'vue-router'

// Containers
import Full from '@/containers/Full'

// Views
import Dashboard from '@/views/Dashboard'
import Shop from '@/views/Shop'
import User from '@/views/User'
import Checklist from '@/views/Checklist'
import ChecklistCreateOrModify from '@/views/ChecklistCreateOrModify'
import UserChecklistStart from '@/views/UserChecklistStart'
import UserChecklist from '@/views/UserChecklist'
import ChecklistResult from '@/views/ChecklistResult'
import Board from '@/views/Board'
import UserBoard from '@/views/UserBoard'
import UserBoardDetails from '@/views/UserBoardDetails'
import UserAlarm from '@/views/UserAlarm'

// Views - Pages
import Login from '@/views/pages/Login'
import ForgotPassword from '@/views/pages/ForgotPassword'
import ResetPassword from '@/views/pages/ResetPassword'
import Page404 from '@/views/pages/Page404'
import Page500 from '@/views/pages/Page500'

Vue.use(Router)

export default new Router({
  mode: 'history',
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: '/login',
      name: 'Login',
      meta: { needsAuth: false },
      component: Login
    },
    {
      path: '/forgot-password',
      name: 'Forgot-Password',
      meta: { needsAuth: false },
      component: ForgotPassword
    },
    {
      path: '/reset-password',
      name: 'Reset-Password',
      meta: { needsAuth: false },
      component: ResetPassword,
      props: true
      // props: route => ({ query: route.query.token })
    },
    {
      path: '/',
      redirect: '/dashboard',
      name: 'Home',
      component: Full,
      children: [
        {
          path: 'dashboard',
          meta: { needsAuth: true },
          name: '',
          component: Dashboard
        },
        {
          path: 'shop',
          name: '',
          component: Shop
        },
        {
          path: 'user',
          name: '',
          component: User
        },
        {
          path: 'checklist',
          name: ' ',
          component: Checklist
        },
        {
          path: 'checklist/create',
          name: ' ',
          component: ChecklistCreateOrModify
        },
        {
          path: 'checklist/:id',
          name: ' ',
          component: ChecklistCreateOrModify
        },
        {
          path: 'checklist-result/:id',
          name: ' ',
          component: ChecklistResult,
          props: true
        },
        {
          path: 'checklist-shop-result/:id',
          name: '  ',
          component: ChecklistResult,
          props: true
        },
        {
          path: 'user-checklist',
          name: ' ',
          component: UserChecklist
        },
        {
          path: 'user-checklist-result/:id',
          name: '  ',
          component: ChecklistResult,
          props: true
        },
        {
          path: 'user-checklist/:id',
          name: ' ',
          component: UserChecklistStart
        },
        {
          path: 'board',
          name: ' ',
          component: Board
        },
        {
          path: 'user-board',
          name: ' ',
          component: UserBoard
        },
        {
          path: 'user-board-details/:board_id/:content_id',
          name: ' ',
          component: UserBoardDetails,
          props: true
        },
        {
          path: 'user-alarm',
          name: ' ',
          component: UserAlarm,
          props: true
        }
      ]
    },
    {
      name: 'error',
      path: '/error',
      component: Page500,
      props: true
    },
    {
      name: '404',
      path: '*',
      component: Page404
      // redirect: '/'
    }
  ]
})



