<template>
  <div class="wrapper">
    <div class="animated fadeIn">
      <b-card header="  ">
        <b-btn class="mb-3"
          size="sm"
          v-if="userWritableBoards.length >= 1"
          :variant="collapseCreateVariant"
          @click="showCollapse = !showCollapse"
          :class="showCollapse ? 'collapsed' : null"
          aria-controls="collapseCreate"
          :aria-expanded="showCollapse ? 'true' : 'false'"
        >{{ collapseButtonText }}
        </b-btn>
        <!--   -->
        <b-card border-variant="light" v-show="showCollapse">
          <b-collapse id="collapseCreate" class="mt-2" v-model="showCollapse">
            <v-select label="title" placeholder=" "
              v-model="selectedBoard"
              :options="userWritableBoards"
              :reset-on-options-change="true"
            ></v-select>
            <comment-input
              @cancel="showCollapse = !showCollapse"
              @save="onCommentInputSave"
              :comment="newPost"></comment-input>
            <!-- <comment-input></comment-input> -->
          </b-collapse>
        </b-card>
        <!--  toolbox -->
        <div class="row">
          <div class="col-md-3">
            <b-form-group horizontal label=" " :label-cols="6">
              <b-form-select :options="pageOptions" v-model="perPage" />
            </b-form-group>
          </div>
          <div class="col-md-3">
            <b-form-group horizontal label="" :label-cols="3">
              <b-input-group>
                <b-form-input v-model="filter" placeholder=" " />
                <b-input-group-button slot="right" v-if="filter">
                  <b-btn @click="filter = null" variant="secondary"><i class="fa fa-remove"></i></b-btn>
                </b-input-group-button>
              </b-input-group>
            </b-form-group>
          </div>
          <div class="col-md-2">
            <b-button :disabled="!sortBy" @click="sortBy = null"></b-button>
          </div>
          <div class="col-md-4">
            <b-pagination align="right" :total-rows="totalRows" :per-page="perPage" v-model="currentPage" />
          </div>
        </div>
        <!--  -->
        <b-table striped hover show-empty responsive
          :empty-text="messages.emptyText"
          :items="rootPosts"
          :fields="fields"
          :current-page="currentPage"
          :per-page="perPage"
          :filter="filter"
          :sort-by.sync="sortBy"
          :sort-desc.sync="sortDesc"
          @filtered="onFiltered"
        >
          <template slot="board_title" scope="row">
            <b-link @click.prevent="filter = row.item.board_title"
              v-b-tooltip.hover title="    ."
            >{{ row.item.board_title }}</b-link>
          </template>
          <template slot="created_dt" scope="row">
            {{ row.item.created_dt | moment('YYYY-MM-DD')  }}
          </template>
          <template slot="files" scope="row">
            <b-btn variant="outline-info" size="sm" @click.stop="download(row.item,row.index,$event.target)"></b-btn>
          </template>
          <template slot="actions" scope="row">
            <b-btn variant="outline-info" size="sm" @click.stop="details(row.item,row.index,$event.target)"></b-btn>
          </template>
        </b-table>
      </b-card>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { mapGetters, mapActions } from 'vuex'
import CommentInput from './CommentInput'

const fields = {
  board_title: { label: '', sortable: true, 'class': 'text-center' },
  creator: { label: '', sortable: true, 'class': 'text-center' },
  created_dt: { label: '', sortable: true, 'class': 'text-center' },
  title: { label: '', sortable: true, 'class': 'text-center' },
  reply_cnt: { label: '', sortable: true, 'class': 'text-center' },
  // files: { label: '' },
  actions: { label: '' }
}

export default {
  name: 'user-board',

  components: {
    CommentInput
  },

  created () {
    if (this.userBoards.length === 0) {
      this.fetchUserBoardList()
    }

    if (this.allUserPosts.length === 0) {
      this.fetchUserPosts()
    }
  },

  data () {
    return {
      showCollapse: false,
      fields: fields,
      currentPage: 1,
      perPage: 5,
      totalRows: 0,
      pageOptions: [
        { text: 5, value: 5 },
        { text: 10, value: 10 },
        { text: 15, value: 15 }
      ],
      sortBy: null,
      sortDesc: false,
      filter: null,
      selectedBoard: null,
      form: {
        id: null,
        title: null,
        content: null,
        file: null
      }
    }
  },

  watch: {
    // selectedBoard (board) {
    //   this.filter = board.title
    // }
  },

  methods: {
    onFiltered (filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
    },

    onCommentInputSave () {
      this.showCollapse = !this.showCollapse
    },

    details (item, index, button) {
      Vue.router.push({name: ' ', params: { board_id: item.board_id, content_id: item.content_id }})
    },

    ...mapActions([
      'fetchUserBoardList',
      'fetchUserPosts'
    ])
  },

  computed: {
    newPost () {
      if (!this.selectedBoard) {
        return { board_title: null, board_id: null, isNew: true }
      } else {
        return { board_title: this.selectedBoard.title, board_id: this.selectedBoard.board_id, isNew: true }
      }
    },

    collapseCreateVariant () {
      return this.showCollapse ? 'danger' : 'primary'
    },

    collapseButtonText () {
      const caption = this.form.id ? '' : ''
      const captionOkCancel = this.showCollapse ? '' : ''

      return ` ${caption}` + ` ${captionOkCancel}`
    },

    boardOptions () {
      return [
        { value: null, text: ' .' }
      ]
    },

    rootPosts () {
      return this.allUserRootPosts
    },

    ...mapGetters({
      messages: 'messages',
      allUserPosts: 'getAllUserPosts',
      allUserRootPosts: 'getAllUserRootPosts',
      filterUserGroupPosts: 'getAllUserGroupPosts',
      userBoards: 'getAllUserBoards',
      userWritableBoards: 'getAllUserWritableBoards'
    })
  }
}
</script>


