<template>
  <div class="wrapper">
    <div class="animated fadeIn">
      <b-card header=" ">
        <!--  toolbox -->
        <div class="row">
          <div class="col-md-3">
            <b-form-group horizontal label=" " :label-cols="6">
              <b-form-select :options="pageOptions" v-model="perPage" />
            </b-form-group>
          </div>
          <div class="col-md-3">
            <b-form-group horizontal label="" :label-cols="3">
              <b-form-input v-model="filter" placeholder=" " />
            </b-form-group>
          </div>
          <div class="col-md-2">
            <b-button :disabled="!sortBy" @click="sortBy = null"></b-button>
          </div>
          <div class="col-md-4">
            <b-pagination align="right" :total-rows="totalRows" :per-page="perPage" v-model="currentPage" />
          </div>
        </div>
        <!--  -->
        <b-table striped hover show-empty responsive
          :empty-text="messages.emptyText"
          :items="allAlarms"
          :fields="fields"
          :current-page="currentPage"
          :per-page="perPage"
          :filter="filter"
          :sort-by.sync="sortBy"
          :sort-desc.sync="sortDesc"
          @filtered="onFiltered"
        >
          <template slot="created_dt" scope="row">
            {{ row.item.created_dt | moment('YYYY-MM-DD HH:mm')  }}
          </template>
          <template slot="actions" scope="row">
            <b-btn variant="outline-info" size="sm" @click.stop="updateAlarm(row.item)"></b-btn>
          </template>
        </b-table>
      </b-card>
    </div>
    <!--    -->
    <b-modal v-if="modalDetails" id="modalShowInfo" ok-title="" cancel-title="" @ok="updateAlarm(modalDetails)">
      <h5 slot="modal-header">{{ modalDetails.title }}</h5>
      <p>{{ modalDetails.memo }}</p>
    </b-modal>
  </div>
</template>

<script>
import Vue from 'vue'
import { mapGetters, mapActions } from 'vuex'

const fields = {
  // alarm_type: { label: '', sortable: true, 'class': 'text-center' },
  memo: { label: '', sortable: true },
  // title: { label: '', sortable: true, 'class': 'text-center' },
  // creator_name: { label: '', sortable: true, 'class': 'text-center' },
  created_dt: { label: '', sortable: true, 'class': 'text-center' },
  actions: { label: '' }
}

export default {
  name: 'alarm',

  created () {
    if (this.allAlarms.length === 0) {
      this.fetchAlarmList()
    }
  },

  data () {
    return {
      fields: fields,
      currentPage: 1,
      perPage: 5,
      totalRows: 0,
      pageOptions: [
        { text: 5, value: 5 },
        { text: 10, value: 10 },
        { text: 15, value: 15 }
      ],
      sortBy: null,
      sortDesc: false,
      filter: null,
      modalDetails: null
    }
  },

  methods: {
    onFiltered (filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
    },

    details (item, index, button) {
      this.modalDetails = Vue.util.extend({}, item)

      setTimeout(() => {
        this.$root.$emit('bv::show::modal', 'modalShowInfo', button)
      }, 300)
    },

    ...mapActions([
      'fetchAlarmList',
      'updateAlarm'
    ])
  },

  computed: {
    ...mapGetters({
      messages: 'messages',
      allAlarms: 'getAllAlarms'
    })
  }
}
</script>


