<template>
  <div class="wrapper">
    <div class="animated fadeIn">
      <div class="row">
        <div class="col-12">
          <b-card header=" ">
            <b-btn v-b-modal.modalCreateShop href="#" variant="primary"> </b-btn>
            <br><br>
            <!--  toolbox -->
            <div class="row">
              <div class="col-md-3">
                <b-form-group horizontal label=" " :label-cols="6">
                  <b-form-select :options="pageOptions" v-model="perPage" />
                </b-form-group>
              </div>
              <div class="col-md-3">
                <b-form-group horizontal label="" :label-cols="3">
                  <b-form-input v-model="filter" placeholder=" " />
                </b-form-group>
              </div>
              <div class="col-md-2">
                <b-button :disabled="!sortBy" @click="sortBy = null"></b-button>
              </div>
              <div class="col-md-4">
                <b-pagination align="right" :total-rows="totalRows" :per-page="perPage" v-model="currentPage" />
              </div>
            </div>
            <!--  -->
            <b-table striped hover show-empty responsive
              :empty-text="messages.emptyText"
              :items="items"
              :fields="fields"
              :current-page="currentPage"
              :per-page="perPage"
              :filter="filter"
              :sort-by.sync="sortBy"
              :sort-desc.sync="sortDesc"
              @filtered="onFiltered"
            >
              <template slot="actions" scope="row">
                <!--<b-btn variant="outline-info" size="sm" @click.stop="details(row.item,row.index,$event.target)"></b-btn>-->
                <b-btn variant="outline-secondary" size="sm" @click.stop="modify(row.item,row.index,$event.target)"></b-btn>
                <b-btn variant="outline-danger" size="sm" @click.stop="remove(row.item,row.index,$event.target)"></b-btn>
              </template>
            </b-table>
          </b-card>
        </div>
      </div>
    </div>

    <!--     -->
    <b-modal id="modalShowInfo"
      @hide="resetModal"
    >
      <h4 slot="modal-header">Index: {{ modalDetails.index }}</h4>
      <pre>{{ modalDetails.data }}</pre>
    </b-modal>
    <!--  /  -->
    <b-modal id="modalCreateShop"
      :title="form.title"
      :header-bg-variant="modalVariants.headerBgVariant"
      :header-text-variant="modalVariants.headerTextVariant"
      :no-close-on-backdrop="true"
      :no-close-on-esc="true"
      @hide="initializeForm"
      @shown="refreshGmap"
      hide-footer
    >
      <google-map
        :gmap-default-position="gmapDefaultPosition"
        :gmap-show-auto-complete="gmapShowAutoComplete"
        :gmap-search-text="gmapSearchText"
        @changed="onGmapChanged"
      ></google-map>
      <br><br>
      <b-form @submit.prevent="onSubmit" novalidate>
        <b-form-group
          description=""
          label=""
          :label-cols="modalLabelCols"
          :horizontal="modalInputHorizontal"
          :feedback="validation.firstError('form.name')"
        >
          <b-form-input type="text"
            :state="!validation.firstError('form.name')"
            :autocomplete="'off'"
            v-model="form.name"
            required placeholder=" "></b-form-input>
        </b-form-group>
        <b-form-group
          description=""
          label=""
          :label-cols="modalLabelCols"
          :horizontal="modalInputHorizontal"
          :feedback="validation.firstError('form.address')"
        >
          <b-form-input type="text"
            :state="!validation.firstError('form.address')"
            :autocomplete="'off'"
            v-model="form.address"
            required placeholder=" "></b-form-input>
        </b-form-group>
        <b-form-group
          description=""
          label=""
          :label-cols="modalLabelCols"
          :horizontal="modalInputHorizontal"
          :feedback="validation.firstError('form.tel')"
        >
          <b-form-input type="text"
            :state="!validation.hasError('form.tel')"
            :autocomplete="'off'"
            v-model="form.tel"
            required placeholder=" "></b-form-input>
        </b-form-group>
        <b-form-group
          description=""
          label=""
          :label-cols="modalLabelCols"
          :horizontal="modalInputHorizontal"
          :feedback="validation.firstError('form.memo')"
        >
          <b-form-textarea id="memoInput"
            :state="!validation.hasError('form.memo')"
            v-model="form.memo"
            placeholder=" "
            :rows="3"
            :max-rows="6">
          </b-form-textarea>
        </b-form-group>

        <b-container>
          <b-row>
            <b-col><b-btn block variant="primary" type="submit"></b-btn></b-col>
            <b-col><b-btn block variant="secondary" @click="initializeForm" type="reset"></b-btn></b-col>
            <b-col><b-btn block variant="danger" @click="hideModal"></b-btn></b-col>
          </b-row>
        </b-container>
      </b-form>
    </b-modal>
  </div>
</template>

<script>
import GoogleMap from '../components/GoogleMap'
import { mapGetters, mapActions } from 'vuex'
import { Validator } from 'simple-vue-validator'

const fields = {
  name: { label: '', sortable: true, 'class': 'text-center' },
  tel: { label: '', 'class': 'text-center' },
  actions: { label: '' }
}

export default {
  name: 'shop',
  data () {
    return {
      gmapDefaultPosition: undefined,
      gmapShowAutoComplete: true,
      gmapSearchText: undefined,
      fields: fields,
      currentPage: 1,
      perPage: 5,
      totalRows: 0,
      pageOptions: [
        { text: 5, value: 5 },
        { text: 10, value: 10 },
        { text: 15, value: 15 }
      ],
      sortBy: null,
      sortDesc: false,
      filter: null,
      modalDetails: { index: '', data: '' },
      form: null,
      modalLabelCols: 2,
      modalInputHorizontal: true
    }
  },

  validators: {
    'form.name' (value) {
      return Validator.value(value).required('')
    },
    'form.tel' (value) {
      return Validator.value(value).digit(' ').minLength(7, ' . 7 ')
    },
    'form.memo' (value) {
      return Validator.value(value).maxLength(40, '40 ')
    }
  },

  components: {
    GoogleMap
  },

  methods: {
    ...mapActions([
      'fetchShopList',
      'createShop',
      'updateShop',
      'deleteShop'
    ]),

    details (item, index, button) {
      this.modalDetails.data = JSON.stringify(item, null, 2)
      this.modalDetails.index = index
      this.$root.$emit('bv::show::modal', 'modalShowInfo', button)
    },

    modify (item, index, button) {
      this.form.title = ' '
      this.form.id = item.id
      this.form.name = item.name
      this.form.address = item.address
      this.form.tel = item.tel
      this.form.memo = item.memo
      this.form.lat = item.lat
      this.form.lng = item.lng

      if (this.form.lat && this.form.lng) {
        this.gmapDefaultPosition = {
          lat: this.form.lat,
          lng: this.form.lng
        }
      } else {
        this.gmapDefaultPosition = undefined
      }

      this.gmapSearchText = undefined

      this.$root.$emit('bv::show::modal', 'modalCreateShop', button)
    },

    remove (item, index, button) {
      if (confirm(' ?')) {
        this.deleteShop(item.id)
      }
    },

    resetModal () {
      this.modalDetails.data = ''
      this.modalDetails.index = ''
    },

    hideModal () {
      this.initializeForm()
      this.$root.$emit('bv::hide::modal', 'modalCreateShop')
    },

    initializeForm () {
      this.form = Object.assign({}, this.form, {
        title: ' ',
        id: null,
        name: null,
        address: null,
        tel: null,
        memo: null,
        lat: null,
        lng: null,
        active: true
      })
    },

    onSubmit () {
      this.$validate()
        .then(success => {
          if (success) {
            if (confirm('?')) {
              if (this.form.id) {
                this.updateShop(this.form)
              } else {
                this.createShop(this.form)
              }
              this.hideModal()
            }
          } else {
            alert('   .')
          }
        })
    },

    onFiltered (filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
    },

    onGmapChanged ({ lat, lng, name, address, tel: telNo }) {
      if (this.form.id) {
        if (!confirm('  ?')) {
          return false
        }
      }

      this.form.name = name
      this.form.address = address

      if (telNo !== undefined) {
        this.form.tel = telNo.replace(/[^0-9]/g, '')
      } else {
        this.form.tel = null
      }

      this.form.lat = lat
      this.form.lng = lng

      if (this.form.lat && this.form.lng) {
        this.gmapDefaultPosition = {
          lat: this.form.lat,
          lng: this.form.lng
        }
      } else {
        this.gmapDefaultPosition = undefined
      }
    },

    refreshGmap () {
      this.$gmapDefaultResizeBus.$emit('resize')
    }
  },

  created () {
    if (this.items.length === 0) {
      this.fetchShopList()
    }

    this.initializeForm()
  },

  computed: {
    ...mapGetters({
      items: 'getAllShops',
      modalVariants: 'modalVariants',
      messages: 'messages'
    })
  }
}
</script>


